header {
    
    padding-top: 7rem;
    overflow: hidden;
}

 .header_container {
    text-align: center;
    height: 100%;
    position: relative;
}


/* ========= CTA ======== */
.cta {
    margin-top: 1.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

